.login {
  display: grid;
  place-items: center;
  height: 100vh;
  background-color: black;
}
.login img {
  width: 50%;
  background-color: black;
}
.login > a {
  padding: 20px;
  background-color: #1db954;
  border-radius: 100px;
  text-decoration: none;
  color: white;
  font-weight: 800;
}
